#todo-item {
    display: inline-block;
    margin-right: 5px;
}

.todo-comp { 
    text-align: left;
}

.todo-comp .todo {
    border-bottom: 1px solid gray;
    display: flex;
    justify-content: space-between;
    padding: 0;
}

.delete-todo {
    color: red;
    /* border: none; */
    display: none;
}

.delete-todo:hover {
    /* color: red; */
    font-weight: bold;
    cursor: pointer;
    /* background-color: white; */
}

.show-del-button {
    display: inline-block;
}

.completed-todo {
    color: lightslategray;
    text-decoration: line-through;
}

.todo-text {
    font-size: 1.2em;
}